
*{
    font-family: Arial, Helvetica, sans-serif;
}

#root{
    overflow: hidden;
}

.main{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    min-height: 100vh;
    background-color: #f1f2f6;
    overflow: hidden;
}

.main-content{
    background-color: white;
    margin: auto;
    border-radius: 1rem;
    padding: 1rem;
}

.thumnail{
    margin: auto;
}

.thumnail .content{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    font-size: 1.08rem;
    width: 100%;
    margin: auto;
}

.thumnail .content b{
    margin: 15px 0 0 0;
    font-size: 1.125rem;
}

.thumnail .content p{
    padding: 0;
