    font-size: 1rem;
}

.bottom{
    width: 100%;
    background-color: #f0f1f6b7;
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    text-align: left;
    padding: .75rem;
    margin: 10px auto;
}

.bottom svg path{
    fill: #9f580a;
}

.bottom svg{
    width: 24px !important;
    height: 24px !important;
}

.icon{
    max-width: 1.5rem;
    /* padding: 5px; */
    margin-right: 10px;
}

 .bottom p{
    margin: 0;
    font-size: 1.125rem;
}

.main .container .button{
    background: #3f83f8;
    margin: 15px auto;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-radius: 10px;
}

.main .container .button a{
