/* POPUP START */
.popup{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    opacity: 0;
    z-index: -99;
    transition: 0.3s ease-in-out;
}

.popup.active{
    opacity: 1;
    z-index: 99;
    transition: 0.3s ease-in-out;
}

.confirm{
    min-height: 100vh;
    overflow: hidden;
    background-color: #f1f2f6;
}

.popup .background{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color:rgba(0, 0, 0, 0.329);
    opacity: 0;
    transition: 0.2s ease-in-out;
}

.popup.active .background{
    opacity: 1;
    transition: 0.2s ease-in-out;
}

.popup .content{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    max-width: 650px;
    height: min-content;
    background-color: white;
    margin: auto;
    border-radius: 5px;
}

.popup .modal-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
    align-items: flex-start;
    -ms-flex-pack: justify;
    justify-content: space-between;
    border-bottom: 1px solid #dee2e6;
    border-top-left-radius: calc(0.3rem - 1px);
    border-top-right-radius: calc(0.3rem - 1px);
    padding: 15px;
}

.popup .modal-header h5{
    color: #111827;
    font-size: 1.25rem;
    line-height: 1.75rem;
}

.popup button.close {
    padding: 0;
    background-color: transparent;
    border: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.popup .close {
    float: right;
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1;
    color: #000;
    text-shadow: 0 1px 0 #fff;
    opacity: .5;
}

.popup label{
    color: #7d7e82;
    font-weight: 400;
    font-size: 14px;
    font-family: Helvetica Neue,Helvetica,Arial,lucida grande,tahoma,verdana,arial,sans-serif;
}

input[type=password] {
    border-radius: 4px!important;
